/* Notification.css */

.notification-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial', sans-serif;
  }
  
  .notification-header {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .mark-all-read {
    background-color: #388E3C;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .notification-messages {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .notification-message {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: #f5f5f5;
    border-radius: 10px;
    cursor: pointer;
  }
  
  .notification-message:hover {
    background-color: #e0e0e0;
  }
  
  .notification-avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 15px;
  }
  
  .notification-avatar img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .notification-content {
    flex-grow: 1;
  }
  
  .notification-sender {
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .notification-body {
    color: #555;
  }
  
  .notification-time {
    font-size: 12px;
    color: #999;
  }
  
  .notification-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .notification-modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    max-width: 500px;
    width: 100%;
    position: relative;
  }
  
  .notification-modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    cursor: pointer;
  }
  
  .notification-modal-header {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .notification-modal-body {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .notification-modal-time {
    font-size: 12px;
    color: #999;
  }
  